import tuiLoadmore from '@/plugins/thorui/components/loadmore/loadmore.vue';
import tuiNomore from '@/plugins/thorui/components/nomore/nomore';
import { questionDetail } from '@/http/api.js';
import { questionAnswers } from '@/http/api.js';
export default {
	components: {
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			id:1,
			detail:[],
			list:[],
			loadding: false,
			pullUpOn: true,
			page: 1,
			order:'',
			total:0,
			isshow:false,
			isud:true,
			isdefaut:'默认排序',
			isSelected:1
		}
	},
	onShow() {
		wx.hideHomeButton()
	},
	onLoad(options) {
		if(options.id){
			this.id = options.id;
		}
		this.getDetail(this.id)
		this.getList(this.id)
	},
	methods: {
		getDetail(id){
			questionDetail(id).then(res => {
				this.detail = res.data;
			});
		},
		getList(id){
			questionAnswers(this.page,this.order,id).then(res => {
				// this.list = res.data.data;
				this.total = res.data.total
				if (!this.pullUpOn) return;
					this.loadding = true;
					if (res.data.data.length == 0) {
						this.loadding = false;
						this.pullUpOn = false;
						return;
					}
					if (res.data.current_page == res.data.last_page) {
						this.loadding = false;
						this.pullUpOn = false;
						this.list = this.list.concat(res.data.data);
						this.page = this.page + 1;
						return;
					}
					this.list = this.list.concat(res.data.data);
					this.page = this.page + 1;
			});
		},
		right(){
			if(this.isshow){
				this.isshow = false
				this.isud = true
			}else{
				this.isshow = true
				this.isud = false
			}
		},
		choose(x){
			this.page = 1
			this.list = []
			this.loadding = false
			this.pullUpOn = true
			this.isshow = false
			this.isud = true
			if(x == 1){
				this.isdefaut = "默认排序"
				this.order = 'default'
				this.isSelected = 1
				this.getList(this.id);
				console.log(this.list)
			}else{
				this.isSelected = 2
				this.isdefaut = "按时间排序"
				this.order = "created"
				this.getList(this.id);
			}
		},
		tohide(){
			this.isshow = false
			this.isud = true
		}
	},
	// 页面上拉触底事件的处理函数
	onReachBottom: function() {
		this.getList(this.id);
	}
}
